* {
    margin: 0;
    padding: 0;
}

.div {
    margin: 0;
    padding: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
}

.img {
    max-width: 1050px;
    width: 100%;
    height: auto;
    padding-left: 20px;
}

.ol {
    padding: 0;
    margin: 0;
    list-style-type: none;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: baseline;
}

.li {
    margin-top: 15px;
    font-size: 1.5em;
}

.li a {
    text-decoration: none;
    color: inherit;
}

.gif {
    padding: 0 0;
    margin: 0 0;
    display: flex;
}

